import Handler from "./Handler";

/* 
* 
* */
export default class EventDispatcher {
    private _events: any;

    hasListener(type: string): boolean {
        let listener: any = this._events && this._events[type];
        return !!listener;
    }

    /**
     * 
     * @param type    
     * @param data    <b></b> p1,p2,p3,...[p1,p2,p3,...]  p  p [p] p  p
     * @return  true false
     */
    public event(type: string, data: any = null): boolean {
        if (!this._events || !this._events[type]) return false;

        let listeners: any = this._events[type];
        if (listeners.run) {
            if (listeners.once) delete this._events[type];
            data != null ? listeners.runWith(data) : listeners.run();
        } else {
            for (let i: number = 0, n: number = listeners.length; i < n; i++) {
                let listener: Handler = listeners[i];
                if (listener) {
                    (data != null) ? listener.runWith(data) : listener.run();
                }
                if (!listener || listener.once) {
                    listeners.splice(i, 1);
                    i--;
                    n--;
                }
            }
            if (listeners.length === 0 && this._events) delete this._events[type];
        }

        return true;
    }

    /**
     *  EventDispatcher 
     * @param type        
     * @param caller    
     * @param listener    
     * @param args        
     * @return  EventDispatcher 
     */
    on(type: string, caller: any, listener: Function, args: any[] = null): EventDispatcher {
        return this._createListener(type, caller, listener, args, false);
    }

    /**
     *  EventDispatcher 
     * @param type        
     * @param caller    
     * @param listener    
     * @param args        
     * @return  EventDispatcher 
     */
    once(type: string, caller: any, listener: Function, args: any[] = null): EventDispatcher {
        return this._createListener(type, caller, listener, args, true);
    }

    /**@internal */
    _createListener(type: string, caller: any, listener: Function, args: any[], once: boolean, offBefore: boolean = true): EventDispatcher {
        //
        offBefore && this.off(type, caller, listener, once);

        //
        let handler: Handler = EventHandler.create(caller || this, listener, args, once);
        this._events || (this._events = {});

        let events: any = this._events;
        //
        if (!events[type]) events[type] = handler;
        else {
            if (!events[type].run) events[type].push(handler);
            else events[type] = [events[type], handler];
        }
        return this;
    }

    /**
     *  EventDispatcher 
     * @param type        
     * @param caller    
     * @param listener    
     * @param onceOnly     true , once 
     * @return  EventDispatcher 
     */
    off(type: string, caller: any, listener: Function, onceOnly: boolean = false): EventDispatcher {
        if (!this._events || !this._events[type]) return this;
        let listeners: any = this._events[type];
        if (listeners != null) {
            if (listeners.run) {
                if ((!caller || listeners.caller === caller) && (listener == null || listeners.method === listener) && (!onceOnly || listeners.once)) {
                    delete this._events[type];
                    listeners.recover();
                }
            } else {
                let count: number = 0;
                let n = listeners.length;
                for (let i: number = 0; i < n; i++) {
                    let item: Handler = listeners[i];
                    if (!item) {
                        count++;
                        continue;
                    }
                    if (item && (!caller || item.caller === caller) && (listener == null || item.method === listener) && (!onceOnly || item.once)) {
                        count++;
                        listeners[i] = null;
                        item.recover();
                    }
                }
                //
                if (count === n) delete this._events[type];
            }
        }

        return this;
    }

    /**
     *  EventDispatcher 
     * @param type     null
     * @return  EventDispatcher 
     */
    offAll(type: string = null): EventDispatcher {
        let events: any = this._events;
        if (!events) return this;
        if (type) {
            this._recoverHandlers(events[type]);
            delete events[type];
        } else {
            for (let name in events) {
                this._recoverHandlers(events[name]);
            }
            this._events = null;
        }
        return this;
    }

    /**
     * callertarget
     * @param    caller caller
     */
    offAllCaller(caller: any): EventDispatcher {
        if (caller && this._events) {
            for (let name in this._events) {
                this.off(name, caller, null);
            }
        }
        return this;
    }

    private _recoverHandlers(arr: any): void {
        if (!arr) return;
        if (arr.run) {
            arr.recover();
        } else {
            for (let i: number = arr.length - 1; i > -1; i--) {
                if (arr[i]) {
                    arr[i].recover();
                    arr[i] = null;
                }
            }
        }
    }
}

/**@private */
class EventHandler extends Handler {

    /**@private handler*/
    protected static _pool: any[] = [];

    constructor(caller: any, method: Function, args: any[], once: boolean) {
        super(caller, method, args, once);
    }

    /**
     * @override
     */
    recover(): void {
        if (this._id > 0) {
            this._id = 0;
            EventHandler._pool.push(this.clear());
        }
    }

    /**
     * Handleroncefalse
     * @param caller    (this)
     * @param method    
     * @param args        
     * @param once        truerecover()true
     * @return handler
     */
    static create(caller: any, method: Function, args: any[] = null, once: boolean = true): Handler {
        if (EventHandler._pool.length) return EventHandler._pool.pop().setTo(caller, method, args, once);
        return new EventHandler(caller, method, args, once);
    }
}