const empty:string = "";
let string = {
    /**
     * null
     * @param s
     * @constructor
     */
    IsNullOrEmpty: function (s: string) {
        return s == null || s == empty;
    },

    /**
     * 
     */
    get empty():string{
        return empty;
    }
};

export {string}